import React, { Component } from 'react'
import {Link} from 'react-router-dom';
import { Pagination } from 'antd';

export default class Quanbu extends Component {
    constructor(){
        super();
        this.state = {
            data:[],
            key:0,
            url:'https://cnodejs.org/api/v1/topics/?tab=all'
        }
    }
    componentDidMount(){
        fetch(this.state.url).then(res=>res.json()).then(res=>{
            {
                res.data.map((item)=>{
                    this.setState({
                        data:res.data
                    })
                })
            }
        })
    }
    jump=(page)=> {
        var url = 'https://cnodejs.org/api/v1/topics/?tab=all&page='+page;
        this.setState({
            url : url
        },function(){
            fetch(this.state.url)
            .then(res=>res.json())
            .then(res=>{
                {
                    res.data.map((item)=>{
                        this.setState({
                            data:res.data
                        })
                    })
                }
            })
        })
    }
    render() {
        return (
            <div>
                <div className="little-content">
                    {
                        this.state.data.map((item,idx)=>
                            <li key={idx}>
                                <img className="img" src={item.author.avatar_url} alt={item.author.loginname}/>
                                <span className="data">{item.reply_count}</span>
                                <span className="title">/{item.visit_count}</span>
                                <Link to={'/topic/'+item.id}>
                                    {item.title}
                                </Link>
                            </li>
                        )
                    }
                </div>
                <div id="pages">
                    <Pagination defaultCurrent={1} total={100} onChange={this.jump} key={this.pages}/>
                </div>
            </div>
        )
    }
}
