import React, { Component } from 'react'
import {Link} from 'react-router-dom';
import store  from '../Store' ;
export default class Login extends Component {
    login(){
        fetch("https://cnodejs.org/api/v1/user/alsotang").then(res=>res.json()).then(res=>{
                {
                    store.dispatch({
                        type:'get_user_infor',
                        userInfor:{
                            avatar_url:res.data.avatar_url,
                            loginname:res.data.loginname
                        }
                    })
                }
            })
    }
    render() {
        return (
            <div>
                <h1>登录</h1>
                <div>
                    用户名：<input type="text" name="username"/>
                    <br/>
                    <br/>
                    <span style={{"float":"left"}}>密码：</span>
                    <input style={{"float":"left","marginLeft":"14px"}} type="pwd" name="pwd"/>
                    <br/>
                    <br/>
                    <Link to='/home/quanbu'>
                        <input style={{"width":"60px","backgroundColor":"blue","color":"#fff"}} type="submit" value="登录" onClick={this.login}/>
                    </Link>
                </div>     
            </div> 
        )
    }
}
